import React, { Component } from 'react';
import $ from 'jquery';
class faq extends Component {
    componentDidMount() {
        var height = $('.faq-list').height();
    }
    
    render() {
        return (
            <div className="" style={{ background: "#fff",marginTop:"10px" }}>
                
                <div className="container">
                    <div className="row " style={{height:"395px",overflow:"auto"}}>
                        <div className="col-sm-4 ">
                            <div className="faq-list" style={{padding:"20px 0px"}}>
                                <a href={`${this.props.match.url}/1`} className="" style={{background: "#eee"}}> <b>How can i become a seller on Kaystore</b></a>
                                <a href={`${this.props.match.url }/2`} className="">I want to reject a product what do i do?</a>
                                <a href={`${this.props.match.url} /3`} className="">Can i pay on delivery?</a>
                                <a href={`${this.props.match.url }/4`} className="">What is your delivery charge like?</a>
                                <a href="#" className="">I want to reject a product what do i do?</a>
                                <a href="#" className="">Can i pay on delivery?</a>
                                <a href="#" className="">What is your delivery charge like?</a>
                                <a href="#" className="">I want to reject a product what do i do?</a>
                                <a href="#" className="">Can i pay on delivery?</a>
                                <a href="#" className="">What is your delivery charge like?</a>
                                <a href="#" className="">I want to reject a product what do i do?</a>
                                <a href="#" className="">Can i pay on delivery?</a>
                                <a href="#" className="">What is your delivery charge like?</a>
                            </div>
                            
                        </div>
                        <div className="col-sm-8">
                            <div>
                                {/* <Route exact path="/1" component="one" />  */}
                                <h3>How can i become a seller on Kaystore</h3>
                               <b> send a proposal Lorem ipsum dolor sit, amet consectetur adipisicing elit. Incidunt iste error eum saepe perspiciatis deserunt voluptatibus ex. Tempora tempore nobis similique et asperiores neque quod. Repudiandae non nisi minus soluta!</b>

                            </div>
                        </div>
                    </div>
                </div>
              
            </div>
        );
    }
}

export default faq;




