import React, { Component } from 'react';

class Login extends Component {
    constructor(){
        super();
        this.state = {
            username: null
        }
    }

    update(e){
        console.log([e.target.name])
        this.setState({
            [e.target.name]: e.target.value
        })
    }
    render() {
        console.log(this.state)
        
        return (
            <div>
                
                <div className="row">
                    <div className="col-sm-4 col-sm-offset-2">
                        
                        <div className="input-group">
                            
                           
                            <input type="text" className="form-control" onChange={this.update.bind(this)} name="username" placeholder="username" />
                            <input type="password" className="form-control" onChange={this.update.bind(this)} name="password" placeholder="password" />
                            
                            <span className="input-group-btn">
                                <button type="button" className="btn btn-default">Go!</button>
                            </span><br />
                            {this.state.username}<br />
                            {this.state.password}
                        </div>
                        
                    </div>
                </div>
                
            </div>
        );
    }
}

export default Login;


