import React, { Component } from 'react';
import $ from "jquery"
import classnames from "classnames"
class Sellersignup extends Component {
    constructor(){
        super();
        this.state ={
            loading:false,
            firstname: "",
            lastname: "",
            email: "",
            phone: "",
            address: "",
            location: "",
            Newsletter: false,
            tac: false,
            error: ""
        }
    }
    componentDidMount() {
    }

    typing(e){
        var node = e.target;
        var prev = $(node).prev().children("span");
        if ($(node).val() === "") prev.html("*"); else prev.html("");
        this.setState({
            [e.target.name] : e.target.value
        });
  

    }

    submit(){
        var data= this.state;
        this.setState({loading:true});

        setTimeout(()=>{
            $.post("http://localhost:3001/api/seller", { ...data }, function (res) {
                this.setState({ error: res,loading:false })
                console.log(res);
            }.bind(this))

        },2000)
 
    }
    render() {
        console.log(this.state)
        return (
            <div className="container seller-account" style={{ overflow: "hidden"}}>
                <div className="row slideInLeft animated" style={{ background: "#fff", padding: "30px 10px", marginTop: "20px"}}>
                    <div className="col-sm-12">
                    <center> <h3> Create a Seller Account</h3>
                     </center>
                    <div style={{ marginBottom: "20px" }}><small >PERSONAL INFORMATION</small>  </div>                      
                    </div>
                    <div className="col-sm-6 ">
                            <div className={classnames("form-group",{'has-error': this.state.error.firstname},"has-feedback")}>
                            <label><span >*</span> First Name <small className="help-block" style={{ position: "relative", display: "inherit" }}> {this.state.error.firstname ? '(' + this.state.error.firstname + ')' : null}</small></label>
                                <input ref="fname" onChange={this.typing.bind(this)} type="text" className="form-control" name="firstname" placeholder="Input field" />
                                {this.state.firstname?<span className="form-control-feedback" style={{color:"green"}}>
                                <i className="fa fa-check"></i>
                                </span>:null}
                            </div>
                            <div className={classnames("form-group", { 'has-error': this.state.error.email }, "has-feedback")}>
                            <label >Email Address <span>*</span> <small className="help-block" style={{ position: "relative", display: "inherit" }}> {this.state.error.email ? '(' + this.state.error.email + ')' : null}</small></label>
                                <input ref="email" type="email" onChange={this.typing.bind(this)}  className="form-control" name="email" id="" placeholder="Input field" />
                                {this.state.email ? <span className="form-control-feedback" style={{ color: "green" }}>
                                    <i className="fa fa-check"></i>
                                </span> : null}
                            </div>
                            <div className={classnames("form-group", { 'has-error': this.state.error.address }, "has-feedback")}>
                                <label > Address <span>*</span></label>
                                <input type="text" ref="address" onChange={this.typing.bind(this)}  name="address" className="form-control" id="" placeholder="Input field" />
                                {this.state.address ? <span className="form-control-feedback" style={{ color: "green" }}>
                                    <i className="fa fa-check"></i>
                                </span> : null}
                            </div>
                          
                    </div>
                    <div className="col-sm-6 ">
                        <div className={classnames("form-group", { 'has-error': this.state.error.lastname }, "has-feedback")}>
                            <label >Last Name <span>*</span> <small className="help-block" style={{ position: "relative", display: "inherit" }}> {this.state.error.lastname ? '(' + this.state.error.lastname + ')' : null}</small></label>
                                <input type="text" ref="lname" onChange={this.typing.bind(this)} name="lastname" className="form-control" id="" placeholder="Input field" />
                                {this.state.lastname ? <span className="form-control-feedback" style={{ color: "green" }}>
                                    <i className="fa fa-check"></i>
                                </span> : null}
                            </div>
                        <div className={classnames("form-group", { 'has-error': this.state.error.phone }, "has-feedback")}>
                            <label >Phone number <span>*</span> <small className="help-block" style={{ position: "relative", display: "inherit" }}> {this.state.error.phone ? '(' + this.state.error.phone + ')' : null}</small></label>
                                <input type="number" ref="phone" onChange={this.typing.bind(this)} name="phone" className="form-control" id="" placeholder="Input field" />
                                {this.state.phone ? <span className="form-control-feedback" style={{ color: "green" }}>
                                    <i className="fa fa-check"></i>
                                </span> : null}
                            </div>
                            <label> Business Locatin <span> *</span> </label>
                            <div className={classnames("form-group", { 'has-error': this.state.error.location }, "has-feedback")}>
                            <select name="" className="form-control" required="required" name="location" onChange={this.typing.bind(this)} >
                                    <option value="" ref="location">Please select a state or province</option>
                                </select>
                            </div>
                    </div>
                    <div className="col-sm-12">
                        
                        <div className=" col-sm-6 col-md-6 col-lg-6 zero">
                            <div className="form-group">
                                <input  type="checkbox"  className="" id="" name="newsletter" style={{ marginRight: "5px" }} />
                                <label ><b>Sign up for Newsletter </b></label>
                            </div>
                        </div>
                        <div className=" col-sm-6 col-md-6 col-lg-6 zero">
                            <div className="form-group">
                                <input ref="tac"  type="checkbox" onChange={(e)=>this.setState({[e.target.name]:true})} className="" name="tac" id="" style={{ marginRight: "5px" }} />
                                <label ><b>Accept Terms And Condition </b></label>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm-12">
                        <button className="btn btn-default btn-block" onClick={this.submit.bind(this)} disabled={this.state.loading}>

                            {this.state.loading ? <div><i className="fa fa-spinner fa-spin  fa-fw"></i></div>
                                :"submit"}
                        </button>
                    </div>
                </div>

            </div>
        );
    }
}

export default Sellersignup;


